.footer {
    background: #202020;
    padding-top: 35px;
    padding-bottom: 45px;
    &__inner {
    }

    &__top {
        position: relative;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        padding-bottom: 40px;
        &::after {
            opacity: 0.2;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
    }

    &__title {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 19px;
        color: white;
    }

    &__link {
        color: rgba(white,0.7);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-top: 10px;
    }

    &__bot {
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
    }

    &__bot-left {
        display: flex;
        gap: 60px;
    }

    &__bot-right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
    &__bot-title {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        color: white;
        margin-bottom: 7px;
    }
    &__bot-link {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        color: rgba(white,0.7);
    }
    &__bot-right-link {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        color: white;
        text-decoration: underline;
    }

    &__bot-img {
        margin-top: 20px;
        height: 22px;
    }
}
