.advantages {
    margin-top: 80px;
    padding-bottom: 55px;
    position: relative;

    &__title {
        font-size: 44px;
        text-align: center;
        margin-bottom: 44px;
    }

    &__content {
        display: flex;
        justify-content: space-between;
    }

    &__left {
        position: relative;
    }

    &__img {
        // &--left {
        // }
        &--right {
            position: absolute;
            top: 160px;
            left: 58px;
            border: 10px solid #fff;
        }
    }

    &__text {
        max-width: 701px;
        font-family: Roboto;
        font-size: 18px;
        font-weight: 400;
        line-height: 25px;
        color: $main;
        & + & {
            margin-top: 10px;
        }
    }
}
