.info {
    margin-top: 40px;
    margin-bottom: 80px;

    &__list {
        display: flex;
        justify-content: space-between;
        gap: 37px;
    }

    &__items {
        max-width: 279px;
    }

    &__text {
        font-family: PT Serif;
        font-size: 58px;
        font-weight: 700;
        line-height: 75px;
        color: $gren-elem;
    }

    &__undertext {
        color: black;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
    }
}
