.register {
    background-color: $bg-light;
    padding-top: 48px;
    padding-bottom: 80px;
    input,
    textarea {
        background-color: #fff;
        border: 1px solid $divid;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        color: $text-help;
        padding-left: 14px;
        padding-top: 11px;
        resize: none;
    }
    input {
        height: 44px;
        padding-left: 14px;
        padding-top: 0;
    }
    &__title {
        margin-bottom: 51px;
        text-align: center;
    }
    &__top {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        row-gap: 24px;
        column-gap: 24px;
        margin-bottom: 24px;
    }
    &__middle {
        display: grid;
        grid-template-columns: 153px 1fr;
        column-gap: 24px;
        row-gap: 24px;
    }
    &__middle-items--big {
        grid-column: 2/-1;
        grid-row: 1/3;
    }

    &__bot {
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__bot-checkbox {
        display: flex;
    }
    &__bot-input {
        z-index: -1;
        opacity: 0;
    }
    &__bot-link {
        text-decoration: underline;
        font-family: Roboto;
        font-size: 16px;
        color: $text-help;
        font-weight: 400;
        line-height: 21px;
    }
    &__bot-input + label {
        display: inline-flex;
        align-items: center;
        user-select: none;
    }
    &__bot-input + label::before {
        content: "";
        display: inline-block;
        width: 26px;
        height: 26px;
        flex-shrink: 0;
        flex-grow: 0;
        border: 2px solid $text-help;
        border-radius: 0.25em;
        margin-right: 0.5em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50% 50%;
    }
    &__bot-input:checked + label::before {
        background-image: url("../img/check-mark.svg");
        background-position: center center;
    }
}
