.trans-btn {
    background-color: transparent;
    border: none;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 22px;
    color: white;

    transition: all 0.2s linear;
    &:hover {
        background-color: white;
        color: black;
    }
}
.blue-btn {
    background-color: $blue-elem;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 30px;
    width: 300px;
    height: 60px;
}
